export default {
  data() {
    return {
      list: [
        {
          title: '验光数据',
          id: 0
        },
        {
          title: '视功能检查',
          id: 1
        },
        {
          title: '复查验光',
          id: 2
        },
        {
          title: '眼部检查',
          id: 3
        }
      ]
    }
  },
  methods: {
    handleClick(id) {
      var url = null
      switch (id) {
        case 0:
          url = 'optometry' // 视功能检查
          break
        case 2:
          url = 'review' // 复查验光
          break
        case 3:
          url = 'eye_examination' // 眼部检查
          break
      }
      this.$router.push({ name: url, query: { data: JSON.stringify() }})
    }
  }
}



